/* stylelint-disable */
@font-face {
  font-family: "Popins-reg";
  src: url("../fonts/../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Popins-semb";
  src: url("../fonts/../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.burgundy-btn {
  color: #FFFFFF;
  border-radius: 8px;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.burgundy-btn:hover {
  background: #521424;
}

.orange-btn {
  border-radius: 8px;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  color: black;
  transition: background-color 0.3s ease;
}
.orange-btn:hover {
  background: #C26226;
}

.grey-btn {
  border-radius: 8px;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture {
  width: auto;
  height: auto;
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}

.main {
  flex: 1 1 auto;
}

[class*=__container] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  font-family: "Popins-reg", sans-serif;
  font-style: normal;
  font-size: 20px;
  letter-spacing: 0.02em;
  color: #111111;
  background: transparent;
}
body._lock {
  overflow: hidden;
}

.casino-best {
  background: #FCEDF0;
  margin: 0 0 20px 0;
}
.casino-best._onWhite {
  padding: 15px;
  border-radius: 8px;
}
@media screen and (max-width: 350px) {
  .casino-best._onWhite {
    padding: 5px;
  }
}
.casino-best__all-link {
  display: inline-block;
  font-family: "Popins-semb", sans-serif;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  align-self: center;
  text-align: center;
  padding: 19px 85px 17px;
}
.casino-best__container {
  display: flex;
  flex-direction: column;
  padding: 0;
}
.casino-best__title {
  margin: 0 0 10px 0;
}
.casino-best__text {
  font-size: 15px;
  line-height: 22px;
  color: #111111;
  max-width: 940px;
  margin: 0 0 20px 0;
}
.casino-best__list {
  width: 100%;
  margin: 0 0 35px 0;
  counter-reset: my-counter;
  display: grid;
  grid-template-columns: calc(33.333% - 10px) calc(33.333% - 10px) calc(33.333% - 10px);
  gap: 15px;
}
.casino-best__list._openItem {
  align-items: flex-start;
}
.casino-best__item {
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  border-radius: 12px;
  flex-direction: column;
  background: #FFFFFF;
}
.casino-best__item-link_wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.casino-best__item-link {
  margin: 0 0 5px 0;
}
.casino-best__item-sublink {
  display: inline-block;
  font-size: 10px;
  line-height: 11px;
  color: #808080;
  transition: color 0.3s ease;
}
.casino-best__item-sublink:hover {
  color: #111111;
}
.casino-best__item-wrapper {
  flex: 0 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 10px 20px 10px;
  border-bottom: 3px dotted #FCEDF0;
  position: relative;
}
.casino-best__item-wrapper::before, .casino-best__item-wrapper::after {
  position: absolute;
  pointer-events: none;
  width: 19px;
  height: 19px;
  background: #FCEDF0;
  border-radius: 50%;
  right: -11px;
}
.casino-best__item-wrapper::before {
  right: -20px;
  bottom: -11px;
}
.casino-best__item-wrapper::after {
  top: auto;
  bottom: -11px;
  left: -20px;
}
.casino-best__item-image {
  min-width: 100%;
  border-radius: 8px;
  position: relative;
  margin: 0 0 15px 0;
}
.casino-best__item-image img {
  min-width: 100%;
  border-radius: 8px;
}
.casino-best__item-image::before {
  counter-increment: my-counter;
  content: counter(my-counter) " ";
  position: absolute;
  top: 0;
  left: 0;
  font-family: "Popins-semb", sans-serif;
  background: #691A2E;
  border-radius: 8px 0 8px 0;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: 20px;
  color: #FFFFFF;
}
.casino-best__item-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 25px 0;
  min-width: 164px;
}
.casino-best__item-title {
  font-family: "Popins-semb", sans-serif;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: -0.004em;
  color: #1D1C0C;
  margin: 0 0 8px 0;
}
.casino-best__item-licensed {
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.004em;
  color: #2275D4;
  display: flex;
  align-items: center;
  margin: 0 0 10px 0;
}
.casino-best__item-licensed-one {
  margin: 0 8px 0 0;
}
.casino-best__item-rating {
  display: flex;
  align-items: center;
  margin: 0 0 15px 0;
}
.casino-best__item-rating-star {
  margin: 0 10px 0 0;
  display: flex;
  align-items: center;
}
.casino-best__item-rating-star img {
  display: inline-block;
  margin: 0 6px 0 0;
}
.casino-best__item-rating-star-text {
  font-size: 12px;
  line-height: 12px;
  color: #111111;
  white-space: nowrap;
}
.casino-best__item-rating-comment {
  display: flex;
  align-items: center;
}
.casino-best__item-rating-comment img {
  display: inline-block;
  margin: 0 5px 0 0;
}
.casino-best__item-rating-comment-text {
  font-size: 12px;
  line-height: 12px;
  color: #111111;
  white-space: nowrap;
}
.casino-best__item-link {
  display: inline-block;
  font-size: 15px;
  line-height: 20px;
  color: #FFFFFF;
  padding: 12px 47px 10px;
}
.casino-best__item-reviews {
  align-items: flex-start;
  flex-direction: column;
  display: none;
  flex: 1 1 auto;
}
.casino-best__item-reviews._show {
  display: flex;
}
.casino-best__item-reviews-plus {
  margin: 0 0 20px 0;
}
.casino-best__item-reviews-btn {
  display: inline-block;
  position: absolute;
  bottom: -17px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  padding: 7px 25px 5px;
  font-size: 13px;
  line-height: 20px;
  border-radius: 8px;
  color: #111111;
  background: #FCEDF0;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
}
.casino-best__item-plus, .casino-best__item-minus {
  display: flex;
  flex-direction: column;
}
.casino-best__item-plus-txt, .casino-best__item-minus-txt {
  display: flex;
  align-items: flex-start;
  font-size: 12px;
  line-height: 28px;
  color: #808080;
}
.casino-best__item-plus-txt::before, .casino-best__item-minus-txt::before {
  content: "";
  display: inline-block;
  margin: 6px 8px 0 0;
  min-width: 16px;
  min-height: 16px;
}
.casino-best__item-plus {
  margin: 0 30px 0 0;
}
.casino-best__item-plus-txt::before {
  background: url("../img/home/best/arrow.svg") no-repeat 0% 0%/contain;
}
.casino-best__item-coupon {
  max-width: 240px;
  min-width: 240px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 25px 10px;
  background: url("../img/home/best/coupon-bg-tab.svg") no-repeat 0 50%/cover;
}
.casino-best__item-coupon-title {
  font-family: "Popins-semb", sans-serif;
  font-size: 15px;
  line-height: 18px;
  color: #111111;
  margin: 0 0 5px 0;
}
.casino-best__item-coupon-subtitle {
  text-align: center;
  font-family: "Popins-semb", sans-serif;
  font-size: 21px;
  line-height: 21px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 0 5px 0;
}
.casino-best__item-coupon-text {
  flex: 1 1 auto;
  font-size: 13px;
  line-height: 18px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 0 10px 0;
}
.casino-best__item-coupon-link {
  width: 60%;
  text-align: center;
  display: inline-block;
  padding: 12px 28px 10px;
  color: black;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (max-width: 1240px) {
  .casino-best__list {
    display: grid;
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
    gap: 15px;
    margin: 0 0 30px 0;
  }
}
@media screen and (max-width: 600px) {
  .casino-best__list {
    margin: 0 0 25px 0;
    grid-template-columns: 100%;
  }
  .casino-best__all-link {
    width: calc(100% - 30px);
  }
  .casino-best__text {
    font-size: 13px;
    line-height: 22px;
  }
}